@-webkit-keyframes blink {
  0% {  background-color: #aa6666;   border: 1px solid #cc8888;  
  }
  50% {  background-color: #cc8888;  border: 5px solid #aa6666;  
  }
  100% {  background-color: #aa6666;  border: 1px solid #cc8888;  
  }
}

@keyframes blink {
  0% {  background-color: #aa6666;   border: 1px solid #cc8888;  
  }
  50% {  background-color: #cc8888;  border: 5px solid #aa6666;  
  }
  100% {  background-color: #aa6666;  border: 1px solid #cc8888;  
  }
}

@-webkit-keyframes wiggle {
  0% {-webkit-transform:rotate(0deg);transform:rotate(0deg); top:0px;}
  25% {-webkit-transform:rotate(6deg);transform:rotate(6deg);top:-10px;}
  75% {-webkit-transform:rotate(0deg);transform:rotate(0deg);top:10px;}
  100% {-webkit-transform:rotate(0deg);transform:rotate(0deg);top:0px;}
}

@keyframes wiggle {
  0% {-webkit-transform:rotate(0deg);transform:rotate(0deg); top:0px;}
  25% {-webkit-transform:rotate(6deg);transform:rotate(6deg);top:-10px;}
  75% {-webkit-transform:rotate(0deg);transform:rotate(0deg);top:10px;}
  100% {-webkit-transform:rotate(0deg);transform:rotate(0deg);top:0px;}
}
